

    import {Input} from '../../../src'
    import {defineComponent} from 'vue'

    export default defineComponent({
        name: "demo-input",
        setup() {
            const inputRef = Input.use.ref('myInput')

            return {
                outerClear: () => {
                    inputRef.value!.methods.clear()
                },
                outerFocus: () => {
                    inputRef.value!.methods.focus(false)
                }
            }
        },
    })
